@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: "iconfont";
  src: url("fonts/iconfont.eot");
  src: url("fonts/iconfont.eot?#iefix") format("eot"), url("fonts/iconfont.woff2") format("woff2"), url("fonts/iconfont.woff") format("woff"), url("fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-facebook:before {
  content: "Ó®Å";
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

* {
  box-sizing: border-box;
}

.cf:before,
.cf:after {
  content: "";
  display: block;
}

.cf:after {
  clear: both;
}

.cf {
  zoom: 1;
}

a {
  color: inherit;
  text-decoration: none;
}

.btn-default {
  border-radius: 5px;
  display: inline-block;
}

.btn-border {
  border: 1px solid #fff;
}

@media only screen and (min-width: 1200px) {
  .btn-border {
    transition: all .4s;
    position: relative;
  }
  .btn-border:hover {
    box-shadow: 0 0 2px #fff;
  }
}

@media only screen and (min-width: 1200px) {
  .inner-wrapper {
    padding: 0 22px;
  }
}

.container {
  position: relative;
  padding: 0 15px;
  margin: 0 auto;
}

@media only screen and (min-width: 320px) {
  .container {
    width: 100%;
  }
}

@media only screen and (min-width: 550px) {
  .container {
    width: 544px;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    width: 970px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    width: 1054px;
  }
}

.block {
  display: flex;
}

.header__top-line {
  background: #294773;
  color: white;
  font: 400 11px "Hind";
  padding: 10px 0 13px;
  text-align: right;
}

.header__log-in {
  text-transform: uppercase;
}

.mobile-lang-btn {
  display: none;
}

@media only screen and (max-width: 768px) {
  .mobile-lang-btn {
    display: inline-block;
    width: 28px;
    height: 16px;
    background: #000;
    margin-right: 33px;
    position: relative;
  }
  .mobile-lang-btn:before {
    transition: all 0.3s;
    content: '';
    position: absolute;
    right: -21px;
    top: 6px;
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 6px 6.5px 0 6.5px;
    border-color: #ffffff transparent transparent transparent;
  }
  .mobile-lang-btn img {
    width: 28px;
    height: 16px;
  }
}

.mobile-lang-btn-rov:before {
  transform: rotateX(180deg);
}

.header__lang {
  display: inline-block;
  vertical-align: middle;
  margin-right: 30px;
  line-height: 1em;
}

@media only screen and (max-width: 768px) {
  .header__lang {
    margin-right: 0px;
  }
}

.header__lang .lang__desc {
  position: relative;
  display: inline-block;
  margin-right: 2px;
  transform: translateY(-36%);
}

@media only screen and (max-width: 768px) {
  .lang__list {
    display: none;
    position: absolute;
    top: 40px;
    right: 135px;
    text-align: center;
    background: #fff;
    z-index: 2;
  }
}

@media only screen and (min-width: 769px) {
  .lang__list {
    display: inline-block;
  }
}

@media only screen and (min-width: 769px) {
  .lang__list .lang__item {
    display: inline-block;
    margin-right: 9px;
  }
}

@media only screen and (min-width: 768px) {
  .lang__list .lang__item {
    display: inline-block;
    margin-right: 10%;
  }
}

.lang__list .lang__item:last-of-type {
  margin-right: 0;
}

@media only screen and (max-width: 768px) {
  .lang__list .lang__item a {
    display: block;
    padding: 10px;
  }
}

.lang__list .lang__item a img {
  display: block;
}

.header__log-in {
  padding: 3px 30px;
  color: #fff;
}

.header-bottom {
  padding-top: 32px;
  padding-bottom: 25px;
}

.header__logo {
  display: inline-block;
  margin-left: 18px;
}

.header__menu {
  float: right;
  margin-top: 26px;
}

@media only screen and (max-width: 768px) {
  .header__menu {
    display: none;
  }
}

.header__menu .menu__item {
  font: 400 15px "Hind", sans-serif;
  color: #6f6e6e;
  text-transform: uppercase;
  display: inline-block;
}

@media only screen and (max-width: 992px) {
  .header__menu .menu__item {
    margin-right: 20px;
  }
  .header__menu .menu__item:last-of-type {
    margin-right: 0;
  }
}

@media only screen and (min-width: 992px) {
  .header__menu .menu__item {
    margin-right: 51px;
  }
  .header__menu .menu__item:nth-of-type(5) {
    margin-right: 41px;
  }
  .header__menu .menu__item:last-of-type {
    margin-right: 0;
  }
}

.header__menu .menu__item a {
  display: block;
}

@media only screen and (min-width: 1200px) {
  .header__menu .menu__item a {
    position: relative;
  }
  .header__menu .menu__item a:before, .header__menu .menu__item a:after {
    position: absolute;
    display: inline-block;
    transition: transform 0.3s, opacity 0.2s;
    opacity: 0;
  }
  .header__menu .menu__item a:before {
    left: -10px;
    display: inline-block;
    content: '[';
    transform: translateX(20px);
  }
  .header__menu .menu__item a:after {
    right: -10px;
    content: ']';
    transform: translateX(-20px);
  }
  .header__menu .menu__item a:hover:before, .header__menu .menu__item a:hover:after {
    opacity: 1;
    transform: translateX(0px);
  }
}

.header__menu .is-active {
  color: #294773;
}

.menu-bnt-mobile {
  display: none;
}

@media only screen and (max-width: 768px) {
  .menu-bnt-mobile {
    font: 600 15px "hind";
    color: grey;
    display: inline-block;
    position: relative;
    float: right;
    padding-left: 56px;
    height: 30px;
    line-height: 30px;
    right: 48px;
    top: 15px;
  }
  .menu-bnt-mobile:after {
    content: "\'";
    position: absolute;
    transform: skewX(-30deg);
    top: -6px;
    right: 6px;
  }
  .menu-bnt-mobile div {
    position: absolute;
    left: 0;
    top: 12px;
    width: 40px;
    height: 6px;
    background: #808080;
    border-radius: 5px;
  }
  .menu-bnt-mobile div:before, .menu-bnt-mobile div:after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background: #808080;
    border-radius: 5px;
  }
  .menu-bnt-mobile div:before {
    top: 11px;
  }
  .menu-bnt-mobile div:after {
    bottom: 11px;
  }
}

.mobile-menu {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #fff;
  padding: 0 15px;
}

.mobile-menu span {
  font: 600 55px "Hind";
  color: #294773;
  text-transform: uppercase;
  position: relative;
  display: inline-block;
  margin-top: 3%;
  margin-bottom: 5%;
}

.mobile-menu span:before {
  content: "\'";
  position: absolute;
  transform: skewX(-30deg);
  font-size: 28px;
  font-weight: 700;
  top: 0px;
  right: 16px;
}

.mobile-menu span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 65px;
  height: 2px;
  background: #989898;
  border-radius: 1px;
  top: 100%;
}

.close-mobile-menu {
  margin: 5% auto 0;
  position: relative;
  background-image: url("../img/sprite.png");
  width: 48px;
  height: 53px;
  background-position: 0px 0px;
}

.menu-clone a {
  font: 400 2.5em /1.4em "Hind";
  color: #294773;
}

.mobile-menu-active {
  width: 100%;
  display: block;
}
